import { Component, Host, h, Prop, Event, EventEmitter } from '@stencil/core';
import { HASHTAG } from '../../utils/utils';

@Component({
  tag: 'fle-product-list-item',
  styleUrl: 'fle-product-list-item.scss',
  shadow: false,
})
export class FleProductListItem {
  @Prop() headline: string;
  @Prop() category: string;
  @Prop() description: string;
  @Prop() ctaText: string;
  @Prop() ctaUrl: string = HASHTAG;
  @Prop() mediaUrl: string;
  @Prop() mediaAltText: string;

  @Event() productClicked: EventEmitter<void>;

  render() {
    return (
      <Host class="fle-product-list-item">
        <fle-category>{this.category}</fle-category>
        <h3>{this.headline}</h3>
        <fle-media media-url={this.mediaUrl} media-alt-text={this.mediaAltText}></fle-media>
        <p>{this.description}</p>
        {/* TODO Maybe it's better to use click event instead a tag */}
        {/*<a href={this.ctaUrl} target="_blank">*/}
          <fle-button onButtonClicked={() => this.onClicked()}>
            {this.ctaText}
          </fle-button>
        {/*</a>*/}
      </Host>
    );
  }

  private onClicked() {
    this.productClicked.emit();
  }
}
