:host {
  display: block;
}

.fle-product-list-item {
  display: flex;
  flex-flow: column nowrap;
  justify-content: center;
  align-items: flex-start;
  overflow: hidden;
  padding: 3rem;
  font-weight: 100;
  gap: 10px;

  h3 {
    color: $primary-color;
  }

  // unstile a tag cause we use button for the link
  a,
  a:link,
  a:visited,
  a:hover,
  a:active {
    color: inherit;
    text-decoration: inherit;
    font-weight: inherit;
  }
}
